import React from "react";
import T from 'i18n-react'
import { Link } from "react-router-dom";
import {
  Hero,
  HeroBody,
  HeroFooter,
  Container,
  Box,
  Columns,
  Column,
  Image,
  Button
} from "bloomer";
import FloatLabelPassword from "../components/FloatLabelPassword";
import Footer from "../components/Footer";

import globalStorage from "react-global-storage";
import alertify from "alertify.js";

import "../assets/styles/page-login.css";

import logo from "../assets/images/SVG/capitual-logo.svg";

import cap from "../lib/capsdk";

class ConfirmView extends React.Component {
  state = {
    password: ""
  };

  onChange = e => {
    if (e && e.preventDefault) e.preventDefault();
    const { name, value } = e.target;
    this.setState({ [name]: value });
  };

  frmSubmit = async () => {
    let qs = new URLSearchParams(window.location.search);
    let email = qs.get("email");
    let code = qs.get("token");
    let password = this.state.password;
    let sess_key = globalStorage.state.sess_key
      ? globalStorage.state.sess_key
      : undefined;
    try {
      let res = await cap.requestA("members.confirmEmail", {
        sess_key,
        email,
        code,
        password
      });
      alertify.success(T.translate("pages.confirm.success"));
      globalStorage.setState("sess_key", res);
      this.props.history.push("/dashboard");
    } catch (e) {
      switch (e) {
        case "InvalidToken":
          alertify.okBtn("OK").alert(
            T.translate("pages.confirm.invalidToken")
          );
          break;

        case "InvalidSession":
          alertify.okBtn("OK").alert("This session does not allow this action.");
          break;
      }
    }
  };

  render() {
    return (
      <Hero className="page-login" isFullHeight>
        <HeroBody>
          <Container>
            <Columns isCentered>
              <Column isSize={4} isMarginless>
                <Box>
                  <Columns isMultiline>
                    <Column isSize="auto">
                      <Link to="/">
                        <Image
                          src={logo}
                          isSize="64x64"
                          hasTextAlign="centered"
                          style={{ margin: "0 auto" }}
                        />
                      </Link>
                    </Column>
                    <br />
                    <Column isSize={12}>
                      <FloatLabelPassword
                        strength={true}
                        label={T.translate("pages.confirm.input")}
                        name="password"
                        onChange={this.onChange}
                        type="password"
                        required
                      />
                    </Column>

                    <Column isSize={12}>
                      <Button
                        isColor="primary"
                        isSize="large"
                        onClick={this.frmSubmit}
                        isFullWidth
                      >
                        {T.translate("pages.confirm.submitBtn")}
                      </Button>
                    </Column>
                  </Columns>
                </Box>
              </Column>
            </Columns>
          </Container>
        </HeroBody>
        <HeroFooter>
          <Footer />
        </HeroFooter>
      </Hero>
    );
  }
}

export default ConfirmView;



